<!-- 一个template（组件）下只能有一个并列的div -->
<template>
<div id="app">
	<el-container>
		<el-header>
			<el-row>
				<el-col :span="4">
					<h4>{{ msg }}</h4>
				</el-col>
				<el-col :span="16" class="t-center">
					<headerNav></headerNav>
				</el-col>
				<el-col :span="4" class="t-right">
					<auth></auth>
				</el-col>
			</el-row>
		</el-header>
		<el-main>
			<router-view class="view"></router-view>
		</el-main>
	</el-container>
</div>
</template>

<script>
import headerNav from './component/header-nav.vue'
import auth from './component/auth.vue'
import Vue from 'vue'
import { Row,Col,Container,Header,Main } from 'element-ui'

Vue.use(Row)
Vue.use(Col)
Vue.use(Container)
Vue.use(Header)
Vue.use(Main)

export default {
	data () {
		return {
			msg: 'My Website'
		}
	},
	components: { headerNav, auth }
}
</script>


<style>

@import url('./public/public.css');

#app > section > header{
	position: fixed;
	width: 100%;
	background: #409eff;
	color: #fff;
	line-height: 60px
}
#app > section > main{
	width: 1140px;
	margin: 60px auto 0 auto
}


</style>



